.profile-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  width: 100%; /* Full width */
  max-width: 400px; /* Max width for the form */
  background-color: rgba(103, 143, 106, 0.21); /* Background color */
  border-radius: 20px; /* Rounded corners */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Shadow for depth */
  margin: auto; /* Centering */
  overflow: hidden; /* Ensures no horizontal overflow */
}

.profile-image {
  width: 150px;
  height: 150px;
  background-image: url('/public/default-profile.jpg');
  background-size: cover;
  border-radius: 50%;
  margin-bottom: 20px;
  border: 2px solid rgba(253, 253, 253, 0.56);
}

.profile-field {
  width: 100%;
  margin-bottom: 10px;
}

.profile-field label {
  font-weight: bold;
  color: #ffffff; /* White color for labels */
}

.profile-field input, .profile-field textarea {
  width: 100%; /* Full width inputs */
  padding: 8px;
  border: 1px solid #ccc; /* Light grey border */
  border-radius: 4px; /* Rounded borders for inputs */
  background-color: #fff; /* White background for inputs */
  color: #333; /* Dark text for visibility */
}

.profile-field textarea {
  height: auto; /* Auto-adjust height */
  min-height: 50px; /* Minimum height */
}
